/**
 * Montserrat Font for jsPDF
 *
 * INSTRUES PARA ADICIONAR AS FONTES:
 *
 * 1. Baixe os arquivos TTF:
 *    - Montserrat-Regular.ttf: https://github.com/JulietaUla/Montserrat/raw/master/fonts/ttf/Montserrat-Regular.ttf
 *    - Montserrat-Bold.ttf: https://github.com/JulietaUla/Montserrat/raw/master/fonts/ttf/Montserrat-Bold.ttf
 *
 * 2. Converta para jsPDF:
 *    - Acesse: https://peckconsulting.s3.amazonaws.com/fontconverter/fontconverter.html
 *    - Upload cada TTF e clique em "Create"
 *    - Copie o cdigo base64 gerado
 *
 * 3. Cole as strings base64 abaixo (substituindo as strings vazias)
 *
 * 4. Ative no pdfGenerator.js conforme instrues no README.md
 */

// Cole aqui a string base64 do Montserrat-Regular.ttf
// Exemplo: const MontserratRegular = 'AAEAAAAQAQ....' (string muito longa)
const MontserratRegular =
  ''
// Cole aqui a string base64 do Montserrat-Bold.ttf
// Exemplo: const MontserratBold = 'AAEAAAAQAQ....' (string muito longa)
const MontserratBold =
  ''
/**
 * Registra as fontes Montserrat no documento jsPDF
 * @param {jsPDF} doc - Instncia do jsPDF
 * @returns {boolean} - true se as fontes foram carregadas com sucesso
 */
export function addMontserratFont(doc) {
  // Verificar se as fontes foram convertidas
  if (!MontserratRegular || !MontserratBold) {
    console.warn(
      ' Fontes Montserrat no convertidas ainda. Usando Helvetica como fallback.',
      '\nVeja instrues em: src/core/infrastructure/utils/fonts/README.md',
    )
    return false
  }

  try {
    // Limpar strings base64 (remover espaos e quebras de linha)
    const cleanRegular = MontserratRegular.replace(/\s/g, '')
    const cleanBold = MontserratBold.replace(/\s/g, '')

    // Adicionar Montserrat Regular
    doc.addFileToVFS('Montserrat-Regular.ttf', cleanRegular)
    doc.addFont('Montserrat-Regular.ttf', 'Montserrat', 'normal')

    // Adicionar Montserrat Bold
    doc.addFileToVFS('Montserrat-Bold.ttf', cleanBold)
    doc.addFont('Montserrat-Bold.ttf', 'Montserrat', 'bold')

    console.log(' Fontes Montserrat carregadas no PDF')
    return true
  } catch (error) {
    console.error(' Erro ao carregar fontes Montserrat:', error)
    console.warn(' Usando Helvetica como fallback devido a erro nas fontes')
    return false
  }
}

/**
 * Verifica se as fontes Montserrat esto disponveis
 * @returns {boolean}
 */
export function isMontserratAvailable() {
  return Boolean(MontserratRegular && MontserratBold)
}
